<template>
<div class="app-page page-content-bottom-menu category">
  <page-content class="page-content">
    <div class="top-search vertical-center level-center">
      <div @click="goHome" class="xiao-xi flex-center">
        <img src="static/images/category/home.png"/>
        <!--<van-icon name="wap-home"/>-->
      </div>
      <van-field
        @focus="goSearch"
        clearable
        icon="search"
        placeholder="在这里输入搜索商品"
        @click-icon="$toast('question')"/>
    </div>
    <div class="div-scroll-tab">
      <cube-scroll-nav
        :side=true
        :data="data"
        :options="scrollOptions"
        v-if="data.length">
        <cube-scroll-nav-panel
          v-for="products in data"
          :key="products.name"
          :label="products.name"
          :title="products.name">
          <div class="products">
            <!--<div class="food" v-for="(food, j) in products.foods" :key="j">-->
              <lazy-component  class="food" v-for="(food, j) in products.foods" :key="j">
                <div @click="goRecommendedDetail(1)" class="food-img">
                  <img :src="food.image"/>
                </div>
                <div @click="goRecommendedDetail(1)" class="food-detail">
                  <div class="food-name">
                    <span v-html="food.name"></span>
                  </div>
                  <div class="food-specification">
                    <span>规格:&nbsp;5kg</span>
                  </div>
                  <div class="food-price">
                    <span>￥: 30.9</span>
                  </div>
                </div>
                <div class="food-add" @click="clickAddShopCart">
                  <img src="static/images/commonicon/+1.png"/>
                </div>
              </lazy-component>
            <!--</div>-->
          </div>
        </cube-scroll-nav-panel>
      </cube-scroll-nav>
    </div>
    <popup-add-shop-cart :show.sync="addShopCart.show"></popup-add-shop-cart>
  </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
import CubeScrollNav from '@/components/cube/scroll-nav/scroll-nav'
import CubeScrollNavPanel from '@/components/cube/scroll-nav/scroll-nav-panel'
import CubeScrollNavBar from '@/components/cube/scroll-nav-bar/scroll-nav-bar'
import PopupAddShopCart from '@/components/Popup/popup-add-shop-cart'
export default {
  name: 'category',
  components: { PageContent, PopupAddShopCart, CubeScrollNav, CubeScrollNavPanel, CubeScrollNavBar },
  data () {
    return {
      scrollOptions: {
        click: false,
        directionLockThreshold: 0
      },
      data: [
        {'name': '热卖',
          'type': -1,
          'foods': [
            {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/jz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/mht.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/pg.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}
          ]
        },
        {'name': '蔬菜乐园',
          'type': -1,
          'foods': [
            {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/jz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/mht.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/pg.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}
          ]
        },
        {'name': '新鲜水果',
          'type': -1,
          'foods': [
            {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/jz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/mht.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/pg.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}

          ]
        },
        {'name': '经典套餐',
          'type': -1,
          'foods': [
            {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/jz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/mht.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/pg.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}, {'name': '澳大利亚橘子&nbsp;绿色食品', 'icon': '', 'image': 'static/images/category/yz.png'}
          ]
        }
      ],
      addShopCart: {
        show: false
      }
    }
  },
  computed: {
  },
  methods: {
    goRecommendedDetail (id) {
      this.$router.push({path: '/category/productdetail', query: { id: id }})
    },
    goHome () {
      this.$router.push('/')
    },
    goSearch () {
      this.$router.push('/search')
    },
    // 点击'添加菜篮子' + 号图标
    clickAddShopCart () {
      this.addShopCart.show = true
    }
  },
  created () {
  },
  mounted () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .category{
    .top-search{
      width: 100%;
      height: 100px;
      background-color: rgba(57, 72, 60, 1);
      /deep/ .van-field{
        padding: 0 20px;
        background-color: $whiteffffff;
        line-height: 60px;
        width: 80%;height: 68px;
        border-radius: 0 10px 10px 0;
        .van-cell__value{
          background-color: $whiteffffff;
          display: flex;
          align-items: center;
          width: 100%;
          .van-field__body{
            width: 100%;
            input::-webkit-input-placeholder{
              font-family: $font-family-default;
              color: $black999999;
            }
            i{
              color: $black999999;
            }
          }
        }
      }
      .xiao-xi{
        background-color: $green22ac38;
        width: 80px;height: 68px;
        border-radius: 10px 0 0 10px;
        img{
          width: 65%;
        }
        i{
          color: $whiteffffff;
          font-size: 50px;
        }
      }
    }
    .div-scroll-tab{
      position: relative;
      width: 100%;
      height: calc(100% - 100px);
      padding-top: 20px;
      /deep/ .cube-scroll-nav{
        .cube-sticky{
          >.cube-sticky-fixed{
            .cube-sticky-content{
              .cube-scroll-nav-bar{
                white-space: normal;
                overflow: hidden;
              }
            }
          }
        }
        .cube-scroll-nav-bar-item {
          padding: 0.5rem 0.2rem;
          &_active{
            background: #fff;
            color: $green22ac38;
          }
        }
        .cube-scroll-nav-panel-title {
          padding: 0 0 0 14px;
          height: 40px;
          font-family: $font-family-Medium;
          border-left: 8px solid $green22ac38;
          font-size: 28px;
          color: #666666;
          background: white;
          display: flex;
          align-items: center;
        }
        .cube-scroll-nav-panels{
          .cube-scroll-nav-panel{
            margin-bottom: 30px;
          }
        }
      }
      .products{
        .food{
          margin-top: 40px;
          height: 150px;
          display: flex;
          .food-img{
            width: 150px;
            height: 150px;
            img{ width:150px;height: 100% }
          }
          .food-detail{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: calc(100% - 210px);
            height: 100%;
            padding-left: 15px;
            font-family: $font-family-default;
            .food-name{
              font-size: 29px;
              font-weight: 700;
            }
            .food-specification{
              font-size: 20px;
              color: $black999999;
            }
            .food-price{
              font-size: 29px;
              color: $redffd92d16;
            }
          }
          .food-add{
            width: 50px;height: 100%;
            display: flex;
            flex-direction: column-reverse;
            img{width: 50px;height: 50px;}
          }
        }
      }
    }
  }
</style>



